export default {
	props: {
		showVolume: {
			type: Boolean,
			default: true //true true
		}
	},
	data() {
		return {
			mutedStatus: false, //showMuted==true truefalse
			isOnVolumeBar: false, //
			isShowVolumeBar: false, //
			volume: 1, //1,01
		}
	},
	methods: {
		/**
		 *  @description 
		 */
		switchMuted() {
			let t = this,
				flag = t.mutedStatus;
			if(t.$refs[t.audioRef]) {
				t.$refs[t.audioRef].muted = !flag;
				t.mutedStatus = !flag;
			}
		},
		/**
		 * @description 
		 * @param{flag} //0:mousedown,1:mousemove2:mouseup
		 * */
		changeVolume(flag) {
			let t = this;
			if(flag == 0 || flag == 1) {
				let startY = document.getElementById('vertical-slider').getBoundingClientRect().top; //y
				let clientY = event.clientY; //y
				let offseHeight = t.$refs['vertical-slider'].offsetHeight; //
				t.volume = 1 - (clientY > startY + 1 ? (clientY - startY > offseHeight ? offseHeight : clientY - startY - 1) : 0) / offseHeight;
				t.$refs[t.audioRef].volume = t.volume;//
			} else if(flag == 2) {
				t.dragStatus = false;
				//
				t.isVolumeSlider = false;
				//up
				t.isOnVolumeBar ? "" : t.isShowVolumeBar = false;
			}
		},
	},
	watch: {
		/*
		 * @description 
		 * */
		volume: function(nv, ov) {
			let t = this;
			if(nv == 0) { 
				t.mutedStatus = true;//0
			} else {
				t.$refs[t.audioRef].muted=false;
				t.mutedStatus = false;//
			}
		}
	}
}